}

/* Rotating Earth */
.Animated-area{
  background-color: rgb(134, 200, 255);
  margin: 20px 0px 20px 0px;
}
#earth{
margin-left: 30%
}
#earth {
  width: 400px;
  height: 400px;
  background: url(https://web.archive.org/web/20150807125159if_/http://www.noirextreme.com/digital/Earth-Color4096.jpg);
  border-radius: 50%;
  background-size: 610px;
  box-shadow: inset 8px 36px 80px 36px rgb(0, 0, 0), inset -6px 0 12px 4px rgba(255, 255, 255, 0.3);
  animation-name: rotate-earth;
  animation-duration: 12s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  -webkit-animation-name: rotate-earth;
     -webkit-animation-duration: 12s;
     -webkit-animation-iteration-count: infinite;
     -webkit-animation-timing-function: linear;
}
@keyframes rotate-earth {
  from {
    background-position: 0px 0px;
  }
  to {
    background-position: 610px 0px;
  }
}
@-webkit-keyframes rotate-earth {
  from {
    background-position: 0px 0px;
  }
  to {
    background-position: 610px 0px;
  }
}

