/* Footer  Css */

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,500;0,600;0,700;1,300&display=swap');

/* .container{
    max-width: 1170px;
   
    margin: auto;
} */

.footer-container{
  margin-left: 10%
}
.row{
    display: flex;
    flex-wrap: wrap;
}

ul{
    list-style: none;
}
.footer{
    margin: 10px;
    background-color: #242626;
    padding: 70px 0px;
    
}

.footer-col{
    width: 25%;
    padding: 0 15px;

}

.footer-col h4{
    font-size: 18px;
    color: #ffff;
    text-transform: capitalize;
    margin-bottom: 35px;
    font-weight: 500;
    position: relative;
    padding-left: 40px ;
}

.footer-col h4::before{
    content: '';
    position: absolute;
    left: 40px;
    bottom: -10px;
    background-color: #e91e63;
    height: 2px;
    box-sizing: border-box;
    width: 50px;
}

.footer-col ul li:not(:last-child){
    margin-bottom: 10px;
}
.footer-col ul li a{
    font-size: 16px;
    text-transform: capitalize;
    color: #ffff;
    text-decoration: none;
    font-weight: 300;
    color: #bbb;
    display: block;
    transition: all 0.3s ease;
}

.footer-col ul li a:hover{
    color: #ffff;
    padding-left: 10px;
}
 .social_links {
    display: inline-block;
    height: 35px;
    width: 35px;
    background-color: rgb(255, 255, 255,0.2);
    margin: 0 10px 10px 0;
    border-radius: 50%;
    color: #ffff;
}

.social_links:hover{
    height: 40px;
    width: 40px;
}

/* Responsive */

@media (max-width: 767px){
    .footer-col{
        width: 50%;
        margin-bottom: 30px;
    }
}

@media (max-width: 574px){
    .footer-col{
        width: 100%;
    }
}





